@import url(//cdn.jsdelivr.net/npm/hack-font@3/build/web/hack.css);
* {
  font-family: Hack;
}
html, body, #app {
  height: 100%;
  margin: 0;
}
#app {
  display: block;
}
.login-wrapper {
  padding: 8px;
}
.login, .menu {
  text-align: center;
  margin: auto;
  padding: 10px;
  max-width: 400px;
  border: 1px solid;
}
.login .error, .menu .error {
    color: red;
}
.login .success, .menu .success {
    color: green;
}
.editor {
  height: 100%;
}
.editor .wrapper {
    padding: 8px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
    height: calc(100% - 16px);
}
.editor textarea {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    margin: 0;
    width: 100%;
    border: none;
    overflow: hidden;
    outline: none;
    resize: none;
}
.editor textarea.value-text {
      height: 100%;
}
.editor textarea.header {
      font-size: 40px;
}
.editor .save-info {
    text-align: right;
}
input {
  border: 1px solid;
  padding: 5px;
  margin: 2px;
}
button {
  border: 1px solid;
  background: transparent;
  padding: 5px 10px 5px 10px;
  margin: 2px;
  outline: none;
}
a {
  text-decoration: underline;
  cursor: pointer;
  color: black;
}
.list-wrapper {
  text-align: left;
}
